	module.exports = {"data":{"challengeNode":{"title":"Problem 245: Coresilience","description":["We shall call a fraction that cannot be cancelled down a resilient fraction. Furthermore we shall define the resilience of a denominator, R(d), to be the ratio of its proper fractions that are resilient; for example, R(12) = 4⁄11.","","The resilience of a number d > 1 is then","φ(d)d − 1",", where φ is Euler's totient function.","We further define the coresilience of a number n > 1 as C(n)= ","n − φ(n)n − 1.","The coresilience of a prime p is C(p)","= ","1p − 1.","Find the sum of all composite integers 1 < n ≤ 2×1011, for which C(n) is a unit fraction."],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-245-coresilience","blockName":"Project Euler","tests":[{"text":"<code>euler245()</code> should return 288084712410001.","testString":"assert.strictEqual(euler245(), 288084712410001, '<code>euler245()</code> should return 288084712410001.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler245() {\n  // Good luck!\n  return true;\n}\n\neuler245();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-246-tangents-to-an-ellipse","id":"5900f4621000cf542c50ff74"},"slug":"/coding-interview-prep/project-euler/problem-245-coresilience"}}

